* {
  cursor: default;
}
.contact-form .infWindow {
  display: block;
  width: 70px;
  height: 25px;
  position: absolute;
  top: 0;
  left: 105%;
  font-size: 10px;
  font-family: arial;
  border: 1px solid grey;
  border-radius: 10px;
}
.infWindw {
  position: relative;
}
body {
  overflow-x: hidden;
  background: rgb(250, 250, 250);
}
.container {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 1200px;
}
header {
  height: 500px;
  width: 100%;
}
h1 {
  text-align: center;
  // line-height: 300px;
  padding: 120px 0 55px;
  text-transform: uppercase;
  font-weight: 1000;
  font-size: 80px;
  font-family: 'Hands';
  color: white;
  text-shadow: 5px 5px 5px black;
}
.header-line {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.name-of-site {
  width: 250px;
  p {
    font-weight: 600;
    text-align: center;
    line-height: 60px;
    font-family: office;
    font-size: 30px;
  }
}
.navigation-menu {
  width: calc(100% - 250px);
  height: 50px;
  border-bottom: black solid 3px;
  box-sizing: border-box;
}
.open-close-menu {
  top: 10px;
  left: 10px;
  position: fixed;
  display: block;
  width: 40px;
  height: 40px;
  z-index: 66;
  cursor: pointer;
}
.menu-slider-opened {
  width: 450px;
}
.menu-slider-closed {
  width: 0;
}
.menu-slider {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  background: white;
  z-index: 777;
  overflow-y: scroll;
  transition: ease-in-out 0.3s;
  p {
    font-weight: 600;
  }
  a {
    text-decoration: none;
    color: black;
    cursor: pointer;
    &:hover {
      text-decoration: underline solid black;
    }
  }
  li {
    padding-top: 10px;
  }
  ul {
    margin: 50px;
  }
}

.navi-menu__list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  li {
    a {
      text-align: center;
      margin: 0 auto;
      padding: 0 10px;
      display: block;
      width: 100%;
      height: 100%;
      text-decoration: none;
      color: black;
      cursor: pointer;
      &:hover {
        background: black;
        color: white;
        &::after {
          z-index: 1;
          content: '';
          display: block;
          top: 40px;
          left: 50%;
          transform: rotate(45deg);
          position: absolute;
          width: 20px;
          height: 20px;
          background: black;
        }
      }
    }
    z-index: 5;
    position: relative;
    font-size: 20px;
    line-height: 50px;
  }
}
.header-image {
  margin-top: 20px;
  display: block;
  width: 100%;
  // height: calc(100vh - 200px);
  height: 300px;
  background: center / cover no-repeat url(../img/background-header.jpg);
  p {
    font-family: office;
    font-weight: 600;
    font-size: 25px;
    color: white;
    text-shadow: 2px 2px 2px black;
    text-align: end;
    padding: 0 50px;
  }
}
.catalog-menu {
  margin-top: 20px;
  display: block;
  width: 100%;
  height: 50px;
  border-bottom: black solid 3px;
  box-sizing: border-box;
}
.catalog-menu__container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  li {
    z-index: 5;
    position: relative;
    font-size: 20px;
    line-height: 50px;
    width: max-content;
    a {
      text-decoration: none;
      text-align: center;
      margin: 0 auto;
      padding: 0 10px;
      color: black;
      display: block;
      cursor: pointer;
      width: 100%;
      height: 100%;
      &:hover {
        background: black;
        color: white;
        &::after {
          z-index: 1;
          content: '';
          display: block;
          top: 40px;
          left: 50%;
          transform: rotate(45deg);
          position: absolute;
          width: 20px;
          height: 20px;
          background: black;
        }
      }
    }
  }
}
.catalog-category {
  background-color: white;
  position: relative;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  margin: 20px 45px;
  width: 200px;
  height: 200px;
  a {
    position: absolute;
    height: 100%;
    width: 100%;
    z-index: 7;
    cursor: pointer;
    &:hover ~ .catalog-category__image {
      box-shadow: 1px 1px 3px 1px rgba(0, 0, 0, 0.6); 
    }
  }
}
.catalog-category__image {
  height: 250px;
  width: 100%;
  box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.5); 
  z-index: 2;
}
.catalog-category__name-market {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 140px;
  height: 100px;
  width: 85%;
  box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.5); 
  z-index: 5;
  background: white;
  p {
    text-align: center;
    font-family: office;
    font-size: 15px;
    font-weight: 600;
  }
}
.catalog-category__name {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 170px;
  height: 50px;
  width: 85%;
  box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.5); 
  z-index: 5;
  background: white;
  p {
    text-align: center;
    font-family: office;
    font-size: 17px;
    font-weight: 600;
  }
}
.wr-work-with-us {
  margin-top: 30px;
  h2 {
    font-family: office;
    font-size: 40px;
    font-weight: 600;
    color: rgba(0, 0, 0, 0.7);
    text-align: center;
  }
  p {
    margin-top: 20px;
    font-size: 20px;
    text-align: center;
  }
}
.wr-work__why {
  margin-top: 20px;
  img {
    position: absolute;
    top: 30px;
    left: 50%;
    transform: translateX(-50%);
  }
  p {
    display: block;
    margin: 0 auto;
    width: 150px;
    text-align: center;
    font-family: office;
    font-weight: 600;
    margin-top: 90px;
  }
  ul {
    display: grid;
  }
  li {
    position: relative;
    margin: 0 auto;
    display: block;
    width: 180px;
    height: 180px;
    background: rgba(0, 0, 0, 0.1);
    border-radius: 150px;
    &:nth-of-type(1) {
      grid-column-start: 1;
      grid-column-end: 2;
      grid-row-start: 1;
      grid-row-end: 2;
    }
    &:nth-of-type(2) {
      grid-column-start: 2;
      grid-column-end: 3;
      grid-row-start: 1;
      grid-row-end: 2;
    }
    &:nth-of-type(3) {
      grid-column-start: 3;
      grid-column-end: 4;
      grid-row-start: 1;
      grid-row-end: 2;
    }
    &:nth-of-type(4) {
      grid-column-start: 4;
      grid-column-end: 5;
      grid-row-start: 1;
      grid-row-end: 2;
    }
    &:nth-of-type(5) {
      grid-column-start: 5;
      grid-column-end: 6;
      grid-row-start: 1;
      grid-row-end: 2;
    }
  }
}
footer {
  height: 100px;
  a {
    display: block;
    margin: 0 auto;
    text-align: center;
    text-decoration: none;
    color: black;
    font-family: office;
    font-size: 25px;
    margin-top: 30px;
    cursor: pointer;
  }
}
.contact-us {
  margin-top: 50px;
  padding: 30px 0;
  background: white;
}
.contact-form-market {
  width: 100%;
  border-bottom: 1.5px solid black;
  p {
    font-family: office;
    font-size: 17px;
    font-weight: 600;
    text-align: center;
  }
  form {
    margin-top: 20px;
    height: 200px;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
  }
  .infWindow {
    display: block;
    width: 70px;
    height: 25px;
    position: absolute;
    top: 0;
    left: 105%;
    font-size: 10px;
    font-family: arial;
    border: 1px solid grey;
    border-radius: 10px;
  }
}
.contact-form {
  width: 50%;
  border-right: 1.5px solid black;
  p {
    font-family: office;
    font-size: 17px;
    font-weight: 600;
    text-align: center;
  }
  form {
    margin-top: 20px;
    height: 200px;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: center;
  }
}
.selected-menu {
  position: relative;
  background: black;
    color: white;
    &::after {
      z-index: 2323;
      content: '';
      display: block;
      top: 40px;
      left: 50%;
      transform: translateX(-50%) rotate(45deg);
      position: absolute;
      width: 20px;
      height: 20px;
      background: black;
  }
}
.submit {
  position: relative;
  width: 150px;
  height: 50px;
  border: none;
  // background: none;  
  &:hover {
    background: black;
    color: white;
    &::after {
      z-index: 2323;
      content: '';
      display: block;
      top: 50px;
      left: 50%;
      transform: translateX(-50%) rotate(45deg);
      position: absolute;
      width: 20px;
      height: 20px;
      background: black;
    }
  }
}
.contact-numbers {
  border-left: 1.5px solid black;
  width: 50%;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  p {
    font-family: office;
    font-size: 17px;
    font-weight: 600;
    text-align: center;
  }
  a {
    color: black;
    text-decoration: none;
    cursor: pointer;
    &:hover {
      text-shadow: 0px 0px 0.7px black;
    }
  }
  img {
    width: 30px;
    height: 30px;
    cursor: pointer;
  }
}
.input-name,
.input-number {
  cursor: text;
  font-family: office;
  font-weight: 600;
  width: 220px;
  font-size: 18px;
}
.contact-information {
  display: flex;
  flex-direction: row;
}

// Here is styles for catalog market element!

.catalog-element {
  display: inline-block;
  width: 300px;
  height: 515px;
  box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.5); 
}
.select-size-matr {
  width: 100%;
  border: none;
  box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.5); 
}
.catalog-element__name {
  width: 100%;
  height: 55px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  p {
    text-align: center;
    font-size: 18px;
    font-family: office;
    font-weight: 1000;
  }
}
.catalog-element__image {
  position: relative;
  display: block;
  width: 300px;
  height: 200px;
  background-color: white;
  &::before {
    content: '';
    display: block;
    position: absolute;
    top: 40%;
    transform: translate(-50%);
    left: 150px;
    width: 50px;
    height: 50px;
    background: center / contain no-repeat url('../img/zoom.svg');
    z-index: 10;
  }
  &:hover::after {
    content: '';
    display: block;
    position: absolute;
    top: 40%;
    transform: translate(-50%) scale(1.1);
    left: 150px;
    width: 50px;
    height: 50px;
    background: center / contain no-repeat url('../img/zoom.svg');
    background-color: wheat;
    border-top-left-radius: 100%;
    border-top-right-radius: 100%;
    border-bottom-left-radius: 100%;
    border-bottom-right-radius: 10px;
    cursor: pointer;
    z-index: 15;
  }
}
.catalog-element-inf {
  width: 290px;
  height: 200px;
  // overflow-y: scroll;
  padding: 5px;
  h4 {
    text-align: center;
    font-family: office;
    font-weight: 600;
    font-size: 18px;
    padding: 10px 0;
  }
  p {
    // text-align: center;
    display: inline-block;
    padding: 3px 0;
  }
  span {
    font-weight: 600;
    padding-left: 5px;
  }
}
.catalog-element-price_market {
  width: 100%;
  height: 50px;
  display: flex;
  flex-direction: row;
}
.catalog-element-price {
  width: 70%;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
}
.catalog-element-addtomarket {
  width: 30%;
  background: none;
  border: none;
  box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.5); 
  &:hover {
    box-shadow: -1px -1px 3px 0px rgba(0, 0, 0, 0.5); 
  }
  &:active {
    background: rgba(172, 255, 47, 0.377);
  }
}
.catalog-element-old-price {
  font-family: office;
  font-weight: 600;
  font-size: 18px;
  text-decoration: line-through 2px red;
}
.catalog-element-new-price {
  font-family: office;
  font-weight: 600;
  font-size: 18px;
}
.slider-off {
  height: 0;
}
.slider-on {
  height: 100vh;
}
.catalog-element-slider {
  display: block;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  background: white;
  z-index: 66;
  overflow-y: scroll;
}
.catalog-element-slider__image {
  width: 100%;
  height: 100%;
}
.closed-slider {
  position: absolute;
  width: 50px;
  height: 50px;
  right: 0;
  top: 0;
  border: 3px solid red;
  border-radius: 100%;
  cursor: pointer;
  background: center / contain no-repeat url('../img/close.svg');
  &:active {
    background-color: red;
  }
}
.invalid {
  background: rgba(255, 0, 0, 0.2);
}
.market-trash {
  top: 5px;
  right: 5px;
  z-index: 20;
  width: 30px;
  height: 30px;
  cursor: pointer;
  background: center / contain no-repeat url('../img/trash.svg');
  position: absolute;
  &:hover {
    transform: scale(1.1);
  }
}
.wr-catalog {
  h2 {
    font-family: office;
    font-size: 25px;
    font-weight: 600;
    text-align: center;
  }
}
.scrollInf {
  position: absolute;
  top: 70%;
  right: 10%;
  width: 50px;
  height: 50px;
  background: center / contain no-repeat url('../img/scroll.svg');
}
// payment 
.card,
.cash {
  padding: 8px 40px;
  font-size: 25px;
  font-family: office;
  font-weight: 600;
  position: relative;
}
.cash {
  &::before {
    content: '';
    display: block;
    width: 30px;
    height: 30px;
    left: 0;
    position: absolute;
    background: center / cover no-repeat url('../img/cash.svg');
  }
}
.card {
  &::before {
    content: '';
    display: block;
    width: 30px;
    height: 30px;
    left: 0;
    position: absolute;
    background: center / cover no-repeat url('../img/card.svg');
  }
}
.context-payment {
  font-size: 18px;
  margin: 8px 0;
}

// sorting-styles

.sorting {
  width: 100%;
  display: block;
}
.sorting-elem {
  display: inline-flex;
  flex-direction: column;
  width: 180px;
  height: 40px;
  margin: 10px;
  box-shadow: 1px 1px 3px 0px rgba(0, 0, 0, 0.5); 
  font-family: office;
  font-size: 14px;
  font-weight: 600;
  text-align: center;
  line-height: 40px;
  cursor: pointer;
  &:hover {
    box-shadow: -1px -1px 3px 0px rgba(0, 0, 0, 0.5); 
  }
}
.sorting-active {
  background-color: rgba(0, 0, 0, 0.185);
}