@media all and (max-width: 1200px) {
    .wr-work__why {
        li {
            &:nth-of-type(1) {
              grid-column-start: 1;
              grid-column-end: 2;
              grid-row-start: 1;
              grid-row-end: 2;
            }
            &:nth-of-type(2) {
              grid-column-start: 2;
              grid-column-end: 3;
              grid-row-start: 1;
              grid-row-end: 2;
            }
            &:nth-of-type(3) {
              grid-column-start: 3;
              grid-column-end: 4;
              grid-row-start: 1;
              grid-row-end: 2;
            }
            &:nth-of-type(4) {
              grid-column-start: 1;
              grid-column-end: 3;
              grid-row-start: 2;
              grid-row-end: 3;
            }
            &:nth-of-type(5) {
              grid-column-start: 2;
              grid-column-end: 4;
              grid-row-start: 2;
              grid-row-end: 3;
            }
        }
    }
    .container {
        width: 100%;
    }
}
@media all and (max-width: 990px) { 
    h1 {
        font-size: 50px;
    }
    header {
        height: max-content;
    }
    .header-line {
        flex-direction: column;
    }
    .name-of-site {
        width: 100%;
    }
    .navigation-menu {
        width: 100%;
    }
    .catalog-category {
        margin: 20px 30px;
    }
    .catalog-menu {
    height: auto;
    }
    .catalog-menu__container {
        display: block;
        li {
            display: inline-block;
        }
    }
}
@media all and (max-width: 760px) {
    .navi-menu__list,
    .navigation-menu {
        display: block;
        height: max-content;
        li {
            display: inline-block;
        }
    }
}
@media all and (max-width: 570px) {
    .header-image {
        height: 250px;
    }
    h1 {
        font-size: 40px;
    }
    .header-image p {
        font-size: 18px;
    }
    .wr-work-with-us {
        h2 {
            font-size: 25px;
        }
        p {
            font-size: 15px;
        }
    }
}
@media all and (max-width: 525px) {
    .catalog-category {
        margin: 20px 10px;
    }
    .navi-menu__list, .navigation-menu,
    .catalog-menu__container {
        display: none;
    }
    .wr-work__why {
        li {
            margin-top: 5px;
            &:nth-of-type(1) {
              grid-column-start: 1;
              grid-column-end: 2;
              grid-row-start: 1;
              grid-row-end: 2;
            }
            &:nth-of-type(2) {
              grid-column-start: 2;
              grid-column-end: 3;
              grid-row-start: 1;
              grid-row-end: 2;
            }
            &:nth-of-type(3) {
              grid-column-start: 1;
              grid-column-end: 2;
              grid-row-start: 2;
              grid-row-end: 3;
            }
            &:nth-of-type(4) {
              grid-column-start: 2;
              grid-column-end: 3;
              grid-row-start: 2;
              grid-row-end: 3;
            }
            &:nth-of-type(5) {
              grid-column-start: 1;
              grid-column-end: 3;
              grid-row-start: 3;
              grid-row-end: 4;
            }
        }
    }
    .wr-work__why {
        li {
            width: 140px;
            height: 140px;
        }
        img {
            width: 40px;
            height: 40px;
        }
        p {
            font-size: 12px;
            margin: 75px auto 0;
            width: 130px;
        }
    }
    .contact-information {
        flex-direction: column-reverse;
    }
    .contact-form,
    .contact-numbers {
        width: 100%;
        border: none;
        a {
            margin-top: 10px;
        }
    }
}
@media all and (max-width: 450px) {
    h1 {
        font-size: 30px;
        padding: 80px 0 55px;
    }
    .header-image p {
        font-size: 13px;
    }
    .header-image {
        height: 200px;
    }
    .catalog-category {
        margin: 20px 5px;
    }
    .menu-slider-opened {
        width: 300px;
    }
}
@media all and (max-width: 420px) {
    .catalog-category {
        display: flex;
        margin: 20px auto;
    }
}
@media all and (max-width: 350px) {
    h1 {
        font-size: 25px;
    }
    .header-image p {
        width: 100%;
        text-align: center;
        padding: 0;
    }
    .menu-slider-opened {
        width: 280px;
    }
}